import {fetch as fetchPolyfill} from 'whatwg-fetch';

const LOCAL_JWT_KEY = 'local_json_web_token_key';

function JwtToken() {

}

JwtToken.prototype = {

    constructor: JwtToken,

    existToken: () => sessionStorage.getItem(LOCAL_JWT_KEY) !== null,

    setJwtToken: jwtToken => sessionStorage.setItem(LOCAL_JWT_KEY, jwtToken),

    getJwtToken: () => sessionStorage.getItem(LOCAL_JWT_KEY),

    clearJwtToken: () => sessionStorage.removeItem(LOCAL_JWT_KEY),
}

function JwtNetWork(props) {

    const {contextUrl, jwtToken} = props;

    if (!jwtToken) {
        throw new Error('props need jwtToken object')
    }
    if (contextUrl) {
        this.contextUrl = contextUrl;
    }
    this.jwtToken = jwtToken;
}


JwtNetWork.prototype = {

    constructor: JwtNetWork,


    getHeaders: function () {
        return {
            "mode": "cors",
            "Authorization": this.jwtToken.existToken() ? this.jwtToken.getJwtToken() : null
        }
    },

    fetchJson: function (url, params) {
        return (fetch ? fetch : fetchPolyfill)(`${this.contextUrl}${url}`,
            {
                headers: Object.assign(this.getHeaders(), {"Content-Type": "application/json"}),
                method: 'post',
                body: JSON.stringify(params)
            }
        )
            .then(function (rs) {
                return rs.json();
            })
    },

    fetchPost: function (url, params) {
        return (fetch ? fetch : fetchPolyfill)(`${this.contextUrl}${url}`, {
            headers: Object.assign(this.getHeaders(), {"Content-Type": 'application/x-www-form-urlencoded'}),
            method: 'post',
            body: params
        })
            .then(function (rs) {
                return rs.json();
            });
    },

    fetchGet: function (url, params) {
        let __url = `${this.contextUrl}${url}`;
        if (params) {
            const length = Object.values(params).length;
            Object.entries(params).forEach((ele, index) => {
                __url = `${__url}?${ele[0]}=${ele[1]}`;
                if (length - 1 > index) {
                    __url = `${__url}&`
                }
            })
        }
        return (fetch ? fetch : fetchPolyfill)(__url, {
            headers: this.getHeaders(),
            method: 'get'
        }).then(function (rs) {
            return rs.json();
        })
    },

    fetchCustomMethod: function () {
        throw new Error('....Not yet :)');
    }
}


export default {
    JwtToken,
    JwtNetWork
}
